<template>
  <div class="goodsManage admin-layout">
    <div class="header">
      <el-form :model="form" ref="form" inline>
        <el-form-item label="编码" prop="code">
          <el-input v-model="form.code"></el-input>
        </el-form-item>
        <el-form-item label="名称" prop="name">
          <el-input v-model="form.name"></el-input>
        </el-form-item>
        <el-form-item label="类型" prop="type">
          <el-input v-model="form.type"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="search">查询</el-button>
          <el-button @click="reset">重置</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="main">
      <el-card>
        <div slot="header">
          <el-button type="primary" @click="add">新增</el-button>
        </div>
        <el-table border :data="tableData" ref="tableData">
          <el-table-column type="selection" align="center" />
          <el-table-column label="编码" prop="code" align="center" />
          <el-table-column label="名称" prop="name" align="center" />
          <el-table-column label="面料" prop="material" align="center" />
          <el-table-column label="库存" prop="store" align="center" />
          <el-table-column label="类型" prop="type" align="center" />
          <el-table-column label="图片" prop="img" align="center">
            <template slot-scope="{ row }">
              <el-image :src="row.img" style="height: 100px"></el-image>
            </template>
          </el-table-column>
          <el-table-column label="备注" prop="remark" align="center" />
          <el-table-column label="操作" align="center">
            <template slot-scope="{ row }">
              <el-button type="text" @click="check(row)">查看</el-button>
              <el-button type="text" @click="edit(row)">编辑</el-button>
              <el-button type="text" @click="del(row)">删除</el-button>
            </template>
          </el-table-column>
        </el-table>
        <pagination
          :total="total"
          :page.sync="form.page"
          :limit.sync="form.pageSize"
          @pagination="getList"
        />
      </el-card>
    </div>
  </div>
</template>
<script>
export default {
  name: 'GoodsManage',
  data () {
    return {
      form: {
        code: '',
        name: '',
        type: '',
        page: 1,
        pageSize: 10
      },
      total: 0,
      tableData: [
        {
          code: '562',
          name: 'T恤',
          // desc: '福建省厦门湖里区',
          material: '丝绸',
          store: '522',
          type: 'T恤',
          img: require('@/assets/images/200.jpg'),
          remark: '夏天必买'
        }
      ]
    }
  },
  methods: {
    search () {
      this.form.page = 1
      this.getList()
    },
    reset () {
      this.$refs.form.resetFields()
    },
    getList () {
      console.log('getList')
    },
    add () {
      console.log('新增')
    },
    check (row) {
      console.log('查看')
    },
    edit (row) {
      console.log('编辑')
    },
    del (row) {
      console.log('删除')
    }
  }
}
</script>
<style lang="scss" scoped>
.goodsManage {
}
</style>
